<template>
	<div class="step-c">
    <Form  ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="100">
      <!--   <FormItem
            v-for="(item, index) in formDynamic.items"
            v-if="item.status"
            :key="index"
            :label="'Item ' + item.index"
            :prop="'items.' + index + '.value'"
            :rules="{required: true, message: 'Item ' + item.index +' can not be empty', trigger: 'blur'}">
            key
             -->
        <FormItem
            v-for="(item, index) in formValidate.items"
            v-if="item.status"
            :key="index"
            :label="'No.'+item.index + '  '"
            :prop="'regisseurName' + index"
            :rules="{required: true, message: 'No.'+ item.index +' ', trigger: 'blur'}">
            <i-input v-model="item.value" :placeholder="'No.'+ item.index + ''"></i-input>
            <!-- <i-input v-model="formValidate.regisseurName" placeholder=""></i-input> -->
        </FormItem>
        <FormItem
            v-for="(item, index) in formValidate.items"
            v-if="item.status"
            :key="index"
            :label="'No.'+item.index + '  '"
            :prop="'regisseurGender' + index"
            :rules="{required: true, message: '' + item.index +'', trigger: 'blur'}">
            <RadioGroup v-model="item.value">
                <Radio label="male"></Radio>
                <Radio label="female"></Radio>
            </RadioGroup>

            <Button type="ghost" @click="handleRemove(index)">Delete</Button>
        </FormItem>
        <FormItem label=":" prop="regisseurCapital">
            <i-input v-model="formValidate.regisseurCapital" placeholder=""></i-input>
        </FormItem>
        <FormItem label=":" prop="regisseurNationality">
            <i-input v-model="formValidate.regisseurNationality" placeholder=""></i-input>
        </FormItem>
        <FormItem label=":" prop="regisseurBirthday">
            <DatePicker type="date" placeholder="" v-model="formValidate.regisseurBirthday" style="width: 100%"></DatePicker>
        </FormItem>
        <FormItem label=":" prop="regisseurAddr">
            <i-input v-model="formValidate.regisseurAddr" placeholder=""></i-input>
        </FormItem>
        <FormItem>
            <Row>
                <i-col span="6">
                    <Button type="dashed" long @click="handleAdd" icon="plus-round"></Button>
                </i-col>
            </Row>
        </FormItem>
        <FormItem>
            <Button type="primary" @click="handleSubmit('formValidate')"></Button>
            <Button type="ghost" @click="handleReset('formValidate')" style="margin-left: 8px"></Button>
        </FormItem>
    </Form>
	</div>
</template>

<script type="text/ECMAScript-6">
  export default {
    data () {
        return {
            index: 1,
            formValidate: {
                regisseurName: '',
                regisseurGender: '',
                regisseurCapital: '',
                regisseurNationality: '',
                regisseurBirthday: '',
                regisseurAddr: '',
                items: [
                    {
                        value: '',
                        index: 1,
                        status: 1
                    }
                ]
            },
            ruleValidate: {
                regisseurName: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                regisseurGender: [
                    { required: true, message: '', trigger: 'change' }
                ],
                regisseurCapital: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                regisseurNationality: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                regisseurBirthday: [
                    { required: true, type: 'date', message: '', trigger: 'change' }
                ],
                regisseurAddr: [
                    { required: true, message: '', trigger: 'blur' }
                ]
            }
        };
    },
    created(){
      this.menu();
    },
    methods: {
        menu() {
            window.scrollTo(0,0);
        },
        handleSubmit (name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.$Message.success('Success!');
                } else {
                    this.$Message.error('Fail!');
                }
            });
        },
        handleReset (name) {
            this.$refs[name].resetFields();
        },
        handleAdd () {
            this.index++;
            this.formValidate.items.push({
                value: '',
                index: this.index,
                status: 1
            });
        },
        handleRemove (index) {
            this.formValidate.items[index].status = 0;
        }
    }
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" rel="stylesheet/stylus">
</style>


