<template>
  <div class="bg-pink">
    <div class="main service">
        <Row>
          <i-col span="16">
            <div class="content-circle-bg service-text">
              <h2></h2>
              <p class="margin-tb20">40+10005</p>
              <p>010-1234567</p>
              <p>xiaobai@gmail.com</p>
              <div class="choose">
                <router-link to="/activity/activity-step"><Button type="primary" shape="circle" class="positive" @click="modal2 = true"></Button></router-link>
                <!-- <Button type="primary" shape="circle" class="positive" @click="modal2 = true"></Button> -->
              </div>
            </div>
          </i-col>
          <i-col span="8">
            <v-bar-service></v-bar-service>
          </i-col>
        </Row>
    </div>
    <v-footer :style="{background:'#fff'}"></v-footer>
    <Modal v-model="modal2" >
      <h2 slot="header"></h2>
      <activity-form0></activity-form0>
      <div slot="footer"></div>
    </Modal>
  </div>
</template>

<script type="text/ECMAScript-6">
  import vFooter from 'components/footer/footer';
  import vBarService from 'components/bar/barService';
  import ActivityForm0 from 'components/form/activityForm0';
  export default {
    data(){
      return {
          modal2: false
      };
    },
    components:{
      vFooter,
      vBarService,
      ActivityForm0
    },
    created(){
      this.menu();
    },
    methods: {
      menu() {
        window.scrollTo(0,0);
      }
    }
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/variable';
@import '~common/stylus/mixin';
.main.service
  padding: 100px 0
.service
  .service-text
    padding: 35px 40px 100px
    line-height: 28px
    h2
      font-size: 18px
      font-weight: bold
    p.margin-tb20
      margin: 20px 0
  .choose
    margin: 60px 0 0
    .ivu-btn
      width: 150px
</style>


