<template>
    <div class="step-c">
    <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="100">
        <FormItem label=":" prop="workerProduction">
            <i-input v-model="formValidate.workerProduction" placeholder=""></i-input>
        </FormItem>
        <FormItem label=":" prop="workerCamerist">
            <i-input v-model="formValidate.workerCamerist" placeholder=""></i-input>
        </FormItem>
        <FormItem label=":" prop="workerWriter">
            <i-input v-model="formValidate.workerWriter" placeholder=""></i-input>
        </FormItem>
        <FormItem label=":" prop="workerMakeUpArtist">
            <i-input v-model="formValidate.workerMakeUpArtist" placeholder=""></i-input>
        </FormItem>
        <FormItem label=":" prop="workerFilmCutter">
            <i-input v-model="formValidate.workerFilmCutter" placeholder=""></i-input>
        </FormItem>
        <FormItem label=":" prop="workerArtist">
            <i-input v-model="formValidate.workerArtist" placeholder=""></i-input>
        </FormItem>
        <FormItem label=":" prop="workerAnimation">
            <i-input v-model="formValidate.workerAnimation" placeholder=""></i-input>
        </FormItem>
        <FormItem label=":" prop="workerSpecialEffects">
            <i-input v-model="formValidate.workerSpecialEffects" placeholder=""></i-input>
        </FormItem>
        <FormItem label=":" prop="workerMusic">
            <i-input v-model="formValidate.workerMusic" placeholder=""></i-input>
        </FormItem>
        <FormItem>
            <Button type="primary" @click="handleSubmit('formValidate')"></Button>
            <Button type="ghost" @click="handleReset('formValidate')" style="margin-left: 8px"></Button>
        </FormItem>
    </Form>
    </div>
</template>

<script type="text/ECMAScript-6">
  export default {
    data () {
        return {
            formValidate: {
                workerProduction: '',
                workerCamerist: '',
                workerWriter: '',
                workerMakeUpArtist: '',
                workerFilmCutter: '',
                workerArtist: '',
                workerAnimation: '',
                workerSpecialEffects: '',
                workerMusic: '',

            },
            ruleValidate: {
                workerProduction: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                workerCamerist: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                workerWriter: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                workerMakeUpArtist: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                workerFilmCutter: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                workerArtist: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                workerAnimation: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                workerSpecialEffects: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                workerMusic: [
                    { required: true, message: '', trigger: 'blur' }
                ]
            }
        };
    },
    created(){
      this.menu();
    },
    methods: {
        menu() {
            window.scrollTo(0,0);
        },
        handleSubmit (name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.$Message.success('Success!');
                } else {
                    this.$Message.error('Fail!');
                }
            });
        },
        handleReset (name) {
            this.$refs[name].resetFields();
        }
    }
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" rel="stylesheet/stylus">
</style>


