<template>
	<div class="step-c">
    <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="100">
        <FormItem label=":" prop="videoName">
            <i-input v-model="formValidate.videoName" placeholder=""></i-input>
        </FormItem>
        <FormItem label=":" prop="videoLanguage">
            <Select v-model="formValidate.videoLanguage" placeholder="">
                <Option value="Chiese"></Option>
                <Option value="English"></Option>
            </Select>
        </FormItem>
        <FormItem label=":" prop="videoClassification">
            <CheckboxGroup v-model="formValidate.videoClassification">
                <Checkbox label=""></Checkbox>
                <Checkbox label=""></Checkbox>
                <Checkbox label=""></Checkbox>
            </CheckboxGroup>
        </FormItem>
        <FormItem label=":" prop="videoCategory">
            <CheckboxGroup v-model="formValidate.videoCategory">
                <Checkbox label=""></Checkbox>
                <Checkbox label=""></Checkbox>
                <Checkbox label=""></Checkbox>
                <Checkbox label=""></Checkbox>
                <Checkbox label=""></Checkbox>
                <Checkbox label=""></Checkbox>
                <Checkbox label=""></Checkbox>
                <Checkbox label=""></Checkbox>
                <Checkbox label=""></Checkbox>
                <Checkbox label=""></Checkbox>
                <Checkbox label=""></Checkbox>
                <Checkbox label=""></Checkbox>
                <Checkbox label=""></Checkbox>
                <Checkbox label=""></Checkbox>
                <Checkbox label=""></Checkbox>
            </CheckboxGroup>
        </FormItem>
        <FormItem label=":" prop="videoSubject">
            <CheckboxGroup v-model="formValidate.videoSubject">
                <Checkbox label=""></Checkbox>
                <Checkbox label=""></Checkbox>
                <Checkbox label=""></Checkbox>
                <Checkbox label=""></Checkbox>
                <Checkbox label=""></Checkbox>
                <Checkbox label=""></Checkbox>
                <Checkbox label=""></Checkbox>
                <Checkbox label=""></Checkbox>
                <Checkbox label=""></Checkbox>
                <Checkbox label=""></Checkbox>
                <Checkbox label=""></Checkbox>
            </CheckboxGroup>
        </FormItem>
        <FormItem label=":" prop="videoSubtitle">
            <Select v-model="formValidate.videoSubtitle" placeholder="">
                <Option value="Chiese"></Option>
                <Option value="English"></Option>
            </Select>
        </FormItem>
        <FormItem label=":" prop="videoLength">
            <i-input v-model="formValidate.videoLength" placeholder="00:00:00"></i-input>
        </FormItem>
        <FormItem label=":" prop="soundFormat">
            <Select v-model="formValidate.soundFormat" placeholder="">
                <Option value="Momo">Momo</Option>
            </Select>
        </FormItem>
        <FormItem label=":" prop="videoForms">
            <Select v-model="formValidate.videoForms" placeholder="">
                <Option value="2D">2D</Option>
            </Select>
        </FormItem>
        <FormItem label=":" prop="videoSpecifications">
            <Select v-model="formValidate.videoSpecifications" placeholder="">
                <Option value="Chiese"></Option>
            </Select>
        </FormItem>
        <FormItem label=":" prop="videoColor">
            <Select v-model="formValidate.videoColor" placeholder="">
                <Option value="Chiese"></Option>
                <Option value="English"></Option>
            </Select>
        </FormItem>
        <FormItem label=":" prop="videoFormat">
            <Select v-model="formValidate.videoFormat" placeholder="">
                <Option value="hdv">HDV</Option>
                <Option value="mov">MOV</Option>
            </Select>
        </FormItem>
        <FormItem label=":" prop="filmingCountry">
            <i-input v-model="formValidate.name" placeholder=""></i-input>
        </FormItem>
        <FormItem label=":" prop="onlineDate">
            <DatePicker type="date" placeholder="Select date" v-model="formValidate.onlineDate" style="width: 100%"></DatePicker>
        </FormItem>
        <FormItem label=":"  prop="finishDate">
            <DatePicker type="date" placeholder="Select date" v-model="formValidate.finishDate" style="width: 100%"></DatePicker>
        </FormItem>
        <FormItem>
            <Button type="primary" @click="handleSubmit('formValidate')"></Button>
            <Button type="ghost" @click="handleReset('formValidate')" style="margin-left: 8px"></Button>
        </FormItem>
    </Form>
	</div>
</template>

<script type="text/ECMAScript-6">
  export default {
    data () {
        return {
            formValidate: {
                videoName: '',
                videoLanguage: '',
                videoClassification: [],
                videoCategory: [],
                videoSubject: [],
                videoSubtitle: '',
                videoLength: '',
                soundFormat: '',
                videoForms: '',
                videoSpecifications: '',
                videoColor: '',
                videoFormat: '',
                filmingCountry: '',
                onlineDate: '',
                finishDate: '',
            },
            ruleValidate: {
                videoName: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                videoLanguage: [
                    { required: true, message: '', trigger: 'change' }
                ],
                videoClassification: [
                    { required: true, type: 'array', min: 1, message: '', trigger: 'change' },
                    { type: 'array', max: 1, message: '', trigger: 'change' }
                ],
                videoCategory: [
                    { required: true, type: 'array', min: 1, message: '', trigger: 'change' },
                    { type: 'array', max: 1, message: '', trigger: 'change' }
                ],
                videoSubject: [
                    { required: true, type: 'array', min: 1, message: '', trigger: 'change' },
                    { type: 'array', max: 1, message: '', trigger: 'change' }
                ],
                videoSubtitle: [
                    { required: true, message: '', trigger: 'change' }
                ],
                videoLength: [
                    { required: true, message: '00:00:00', trigger: 'blur' }
                ],
                soundFormat: [
                    { required: true, message: '', trigger: 'change' }
                ],
                videoForms: [
                    { required: true, message: '', trigger: 'change' }
                ],
                videoSpecifications: [
                    { required: true, message: '', trigger: 'change' }
                ],
                videoColor: [
                    { required: true, message: '', trigger: 'change' }
                ],
                videoFormat: [
                    { required: true, message: '', trigger: 'change' }
                ],
                filmingCountry: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                onlineDate: [
                    { required: true, type: 'date', message: '', trigger: 'change' }
                ],
                finishDate: [
                    { required: true, type: 'date', message: '', trigger: 'change' }
                ]
            }
        };
    },
    created(){
      this.menu();
    },
    methods: {
        menu() {
            window.scrollTo(0,0);
        },
        handleSubmit (name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.$Message.success('Success!');
                } else {
                    this.$Message.error('Fail!');
                }
            });
        },
        handleReset (name) {
            this.$refs[name].resetFields();
        }
    }
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" rel="stylesheet/stylus">
</style>


