<template>
  <div class="activity">
    <v-header-focus></v-header-focus>
    <div class="main">
      <div class="content">
        <article class="video-article">
          <h1></h1>
          <p>2017 5 8 </p>
          <h2></h2>
          <dl>
            <dt><div class="text-red"></div></dt>
            <dd>
              <div class="v-pic"><img src="" alt=""></div>
              <p>2017 172017  2049</p>
            </dd>
          </dl>
          <dl>
            <dt><div class="text-red"></div></dt>
            <dd>
              <div class="v-pic"><img src="" alt=""></div>
              <div class="v-pic"><img src="" alt=""></div>
            </dd>
          </dl>
        </article>
        <div class="activity-sign">
          <div class="activity-sign-h">
            <router-link to="/activity/activity-sign"><Button type="primary" shape="circle" class="positive">200</Button></router-link>
          </div>
          <div class="activity-sign-h">
            <Checkbox> <router-link to="/activity/activity-treaty"></router-link></Checkbox>
          </div>
        </div>
      </div>
    </div>
    <v-footer class="bg-pink"></v-footer>
  </div>
</template>

<script type="text/ECMAScript-6">
  import vHeaderFocus from 'components/header/headerFocus';
  import vFooter from 'components/footer/footer';
  export default {
    components:{
      vHeaderFocus,
      vFooter,
    },
    created(){
      this.menu();
    },
    methods: {
      menu() {
        window.scrollTo(0,0);
      }
    }
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" rel="stylesheet/stylus">
@import "~common/stylus/variable"
@import "~common/stylus/mixin"
.activity
  .video-article
    padding: 40px
    line-height: 30px
    h1,h2,h3,h4
      padding: 20px
      text-align: center
    h1
      font-size: 24px
      text-align: center
    h2
      font-size: 20px
    p
      margin: 30px 0
      text-indent: 2rem
    dt
      font-size: 18px
      text-align: center
    .v-pic
      margin: 30px auto
      width: 86%
      min-height: 10px
      border-radius: 2px
      background:$color-positive-background-m
      overflow: hidden
  .activity-sign
    margin: 50px
    text-align: center
    .activity-sign-h
      margin: 20px
</style>


