<template>
    <div class="pop-work-set">
    <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="50">
        <FormItem label="" prop="workSetName">
            <i-input v-model="formValidate.workSetName" placeholder="" autofocus></i-input>
        </FormItem>
        <FormItem label=":" prop="workSetClassification">
            <Select v-model="formValidate.workSetClassification" placeholder="">
                <Option value="no1"></Option>
                <Option value="no2"></Option>
                <Option value="no3"></Option>
            </Select>
        </FormItem>
        <FormItem label="" prop="workSetLabel">
          <CheckboxGroup v-model="formValidate.workSetLabel">
            <Checkbox label="1"></Checkbox>
            <Checkbox label="2"></Checkbox>
            <Checkbox label="3"></Checkbox>
            <Checkbox label="4"></Checkbox>
          </CheckboxGroup>
          <p class="text-hint">{{formValidate.workSetLabel}}</p>
        </FormItem>
        <FormItem label="" prop="workSetIntroduce">
          <Input v-model="formValidate.workSetIntroduce" type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder=''></Input>
        </FormItem>
        <div class="choose">
            <Button type="primary" :loading="loading" @click="handleSubmit('formValidate')" shape="circle"></Button>
            <Button type="ghost" @on-ok="asyncOK" @click="handleReset('formValidate')" shape="circle" class="btn-gray"></Button>
        </div>
    </Form>
    </div>
</template>
<script>
    export default {
        data () {
            return {
                loading: false,
                fruit: [],
                formValidate: {
                    workSetName: '',
                    workSetClassification: '',
                    workSetLabel: [],
                    workSetIntroduce: '',
                },
                ruleValidate: {
                    workSetName: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    workSetClassification: [
                      { required: true, message: '', trigger: 'change' }
                    ],
                    workSetLabel: [
                      { required: true, type: 'array', min: 1, message: '', trigger: 'change' },
                      { type: 'array', max: 3, message: '3', trigger: 'change' }
                    ],
                    workSetIntroduce: [
                        { required: true, message: '', trigger: 'blur' },
                        { type: 'string', min: 20, message: '20', trigger: 'blur' }
                    ]
                }
            };
        },
        created(){
          this.menu();
        },
        methods: {
            menu() {
                window.scrollTo(0,0);
            },
            handleSubmit (name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        this.$Message.success('Success!');
                    } else {
                        this.$Message.error('Fail!');
                    }
                });
            },
            handleReset (name) {
                this.$refs[name].resetFields();
            },
            asyncOK () {
                setTimeout(() => {
                    // this.modal6 = false;
                }, 2000);
            }
        }
    };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" rel="stylesheet/stylus">
@import "~common/stylus/variable";
@import "~common/stylus/mixin";
.pop-work-set
  padding: 10px 60px
  text-align: left
  .choose
    margin: 50px 0 30px
</style>


