<template>
  <div class="bg-pink">
    <div class="main">
      <div class="content">
        <div class="a-page">
        <h1>****</h1>
        <div class="content-circle-bg">
          <div class="step-introduce">
            <dl>
              <dt></dt>
              <dd>
                <ol class="text-level2">
                    <li><span class="level-none">1</span></li>
                    <li><span class="level-none">2</span></li>
                    <li><span class="level-none">3</span>*</li>
                </ol>
              </dd>
            </dl>
          </div>
          <div class="step">
            <Steps :current="current">
                <Step title="1" content=""></Step>
                <Step title="2" content=""></Step>
                <Step title="3" content=""></Step>
                <Step title="4" content=""></Step>
                <Step title="5" content=""></Step>
                <Step title="6" content=""></Step>
            </Steps>
          </div>
          <p class="step-text"> {{ current + 1 }} </p>
          <div class="step-c">
            <router-view></router-view>
          </div>
          <div class="choose">
            <router-link class="nav-item" :to="'/activity/activity-step/step'+ currentPrev" :disabled='current0'><Button type="ghost" shape="circle"  @click="prev"></Button></router-link>
            <router-link class="nav-item" :to="'/activity/activity-step'+stepNum[current+1]"><Button type="primary" shape="circle" @click="next"></Button></router-link>
          </div>
        </div>
        </div>
      </div>
    </div>
    <v-footer class="bg-white"></v-footer>
  </div>
</template>

<script type="text/ECMAScript-6">
  import vFooter from 'components/footer/footer';
  export default {
    data () {
        return {
            current: 0,
            stepNum:[
              '/step1',
              '/step2',
              '/step3',
              '/step4',
              '/step5',
              '/step6',
              '/succeed',
            ],
            currentPrev: 1
        };
    },
    created(){
      this.menu();
    },
    methods: {
        menu() {
          window.scrollTo(0,0);
        },
        prev () {
          if (this.current <= 1) {
            this.currentPrev=1;
            this.current=0;
          } else {
            this.currentPrev -= 1;
            this.current -= 1;
          }
        },
        next () {
          if (this.current == 7) {
              this.current = 0;
          } else {
              this.currentPrev+=1;
              this.current += 1;
          }
        }
    },
    components:{
      vFooter
    }
    };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" rel="stylesheet/stylus">
@import '../../common/stylus/variable';
@import '../../common/stylus/mixin';
.a-page
  margin: 30px auto
  h1
    padding: 15px 0 20px
    font-size: 24px
    font-weight: bold
  .step-introduce
    color: $color-text-article
    .text-level2
      margin: 10px
      li
        padding: 10px
  .step-text
    margin: 10px auto
    text-align: center
  .step
    margin: 60px
    .ivu-steps .ivu-steps-title
      display: none
    .ivu-steps-horizontal .ivu-steps-content
      width: 70px
      padding: 15px 0
      margin-left: -7px
      text-align: center
  .choose
     margin: 60px
  .step-c
    margin: 50px auto
    width: 80%
</style>


