<template>
  <div class="set-collection-item">
    <Upload
      multiple
      type="drag"
      action="//jsonplaceholder.typicode.com/posts/">
      <div class="upload-box">
          <Icon type="ios-cloud-upload" size="52"></Icon>
          <p class="text-hint"><br/></p>
      </div>
    </Upload>
    <div class="set-hint-read">
      <Checkbox v-model="single"> <router-link class="link-underline" to="/activity/activity-treaty"></router-link></Checkbox>
    </div>
  </div>
</template>

<script type="text/ECMAScript-6">
 export default {
  data () {
    return {
      modal1: false,
      defaultList: [
        {
          'name': 'a42bdcc1178e62b4694c830f028db5c0',
          'url': 'https://o5wwk8baw.qnssl.com/a42bdcc1178e62b4694c830f028db5c0/avatar'
        }
      ],
      imgName: '',
      visible: false,
      uploadList: []
    };
  },
  created(){
    this.menu();
  },
  methods: {
    menu() {
      window.scrollTo(0,0);
    },
    handleView (name) {
      this.imgName = name;
      this.visible = true;
    },
    handleRemove (file) {
      const fileList = this.$refs.upload.fileList;
      this.$refs.upload.fileList.splice(fileList.indexOf(file), 1);
    },
    handleSuccess (res, file) {
      file.url = 'https://o5wwk8baw.qnssl.com/7eb99afb9d5f317c912f08b5212fd69a/avatar';
      file.name = '7eb99afb9d5f317c912f08b5212fd69a';
    },
    handleFormatError (file) {
      this.$Notice.warning({
        title: '',
        desc: ' ' + file.name + ' ,  .jpg or .png '
      });
    },
    handleMaxSize (file) {
      this.$Notice.warning({
        title: '',
        desc: '  ' + file.name + ' ,  2M '
      });
    },
    handleBeforeUpload () {
      const check = this.uploadList.length < 1;
      if (!check) {
          this.$Notice.warning({
            title: ''
          });
      }
      return check;
    },
    ok () {
      this.$Message.info('ok');
    },
    cancel () {
      this.$Message.info('');
    }
  },
  mounted () {
    this.uploadList = this.$refs.upload.fileList;
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" rel="stylesheet/stylus">
@import "~common/stylus/variable";
@import "~common/stylus/mixin";
.set-collection-item
  margin: 60px 0
  .set-hint-read
    margin: 20px
    text-align: center
  .upload-box
    line-height: 20px
    p
      padding: 5px 0
</style>


