<template>
	<div class="step-c">
    <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="100">
        <FormItem label=":" prop="productionCompany">
            <i-input v-model="formValidate.productionCompany" placeholder=""></i-input>
        </FormItem>
        <FormItem label=":" prop="productionAddr">
            <i-input v-model="formValidate.productionAddr" placeholder=""></i-input>
        </FormItem>
        <FormItem label=":" prop="productionPhone">
            <i-input v-model="formValidate.productionPhone" placeholder=""></i-input>
        </FormItem>
        <FormItem label=":" prop="productionFaxNumber">
            <i-input v-model="formValidate.productionFaxNumber" placeholder=""></i-input>
        </FormItem>
        <FormItem label=":" prop="productionMail">
            <i-input v-model="formValidate.productionMail" placeholder=""></i-input>
        </FormItem>
        <FormItem label=":" prop="productionPrincipal">
            <i-input v-model="formValidate.productionPrincipal" placeholder=""></i-input>
        </FormItem>
        <FormItem label="" prop="productionGender">
            <RadioGroup v-model="formValidate.productionGender">
                <Radio label="male"></Radio>
                <Radio label="female"></Radio>
            </RadioGroup>
        </FormItem>
        <FormItem label=":" prop="productionDepartment">
            <i-input v-model="formValidate.productionDepartment" placeholder=""></i-input>
        </FormItem>
        <FormItem label=":" prop="productionDepartmentTitle">
            <i-input v-model="formValidate.productionDepartmentTitle" placeholder=""></i-input>
        </FormItem>
        <FormItem>
            <Button type="primary" @click="handleSubmit('formValidate')"></Button>
            <Button type="ghost" @click="handleReset('formValidate')" style="margin-left: 8px"></Button>
        </FormItem>
    </Form>
	</div>
</template>

<script type="text/ECMAScript-6">
  export default {
    data () {
        return {
            formValidate: {
                productionCompany: '',
                productionAddr: '',
                productionPhone: '',
                productionFaxNumber: '',
                productionMail: '',
                productionPrincipal: '',
                productionGender: '',
                productionDepartment: '',
                productionDepartmentTitle: '',

            },
            ruleValidate: {
                productionCompany: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                productionAddr: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                productionPhone: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                productionFaxNumber: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                productionMail: [
                    { required: true, message: '', trigger: 'blur' },
                    { type: 'email', message: '', trigger: 'blur' }
                ],
                productionPrincipal: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                productionGender: [
                    { required: true, message: '', trigger: 'change' }
                ],
                productionDepartment: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                productionDepartmentTitle: [
                    { required: true, message: '', trigger: 'blur' }
                ]
            }
        };
    },
    created(){
      this.menu();
    },
    methods: {
        menu() {
            window.scrollTo(0,0);
        },
        handleSubmit (name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.$Message.success('Success!');
                } else {
                    this.$Message.error('Fail!');
                }
            });
        },
        handleReset (name) {
            this.$refs[name].resetFields();
        }
    }
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" rel="stylesheet/stylus">
</style>


