<template>
  <div class="settings-router">
    <div class="form-reset">
        <div class="form-reset-avator">
          <Avatar size="large" />
          <p class="settings-user-name">JPGPNG6M</p>

        </div>
        <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">
            <FormItem label="" prop="userName">
                <Input v-model="formValidate.userName" placeholder=""></Input>
            </FormItem>
            <FormItem label="" prop="userGender">
                <RadioGroup v-model="formValidate.userGender">
                    <Radio label="male"></Radio>
                    <Radio label="female"></Radio>
                </RadioGroup>
            </FormItem>
            <FormItem label="" prop="city">
                <Cascader :data="data" trigger="hover" placeholder=""></Cascader>
            </FormItem>
            <FormItem label="">
              <DatePicker type="date" multiple placeholder="" style="width: 100%"></DatePicker>
            </FormItem>
            <FormItem label="" prop="desc">
                <Input v-model="formValidate.desc" type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder=""></Input>
            </FormItem>
            <FormItem>
                <Button type="primary" shape="circle" @click="handleSubmit('formValidate')"></Button>
                <Button type="ghost" shape="circle" @click="handleReset('formValidate')" class="btn-gray" style="margin-left: 8px"></Button>
            </FormItem>
        </Form>
    </div>
  </div>
</template>

<script type="text/ECMAScript-6">
export default {
    data () {
        return {
            formValidate: {},
            ruleValidate: {},
            data: [{
                value: 'beijing',
                label: '',
                children: [
                    {
                        value: 'gugong',
                        label: ''
                    },
                    {
                        value: 'tiantan',
                        label: ''
                    },
                    {
                        value: 'wangfujing',
                        label: ''
                    }
                ]
            }, {
                value: 'jiangsu',
                label: '',
                children: [
                    {
                        value: 'nanjing',
                        label: '',
                        children: [
                            {
                                value: 'fuzimiao',
                                label: '',
                            }
                        ]
                    },
                    {
                        value: 'suzhou',
                        label: '',
                        children: [
                            {
                                value: 'zhuozhengyuan',
                                label: '',
                            },
                            {
                                value: 'shizilin',
                                label: '',
                            }
                        ]
                    }
                ],
            }]
        };
    },
    created(){
      this.menu();
    },
    methods: {
        menu() {
            window.scrollTo(0,0);
        },
        handleSubmit (name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.$Message.success('Success!');
                } else {
                    this.$Message.error('Fail!');
                }
            });
        },
        handleReset (name) {
            this.$refs[name].resetFields();
        }
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" rel="stylesheet/stylus" scoped>
@import '../../common/stylus/variable'
@import '../../common/stylus/mixin'
.form-reset-avator
    margin: 0 auto 50px
    text-align: center
    .ivu-avatar
        margin: 20px 0
        circle(100px)
</style>



